.home {
  padding: 3em 0;
}
/* Seccion Movies , tv shows*/
.top-releases {
  width: max-content;/*fit-content*/
  margin: 0 auto 5em auto;
  max-width: 100%;
}
.home h2 {
  margin: 0 0 2em 2em;
  padding: 1em 1.5em;
  width: max-content;/*fit-content*/
  border-radius: 5px;
  text-transform: uppercase;
}
/*Lista de Movies /Covers*/
.release-list {
  display: flex;
  gap: 2em;
  overflow-x: scroll;
  padding: 1em 2em;
}
/* Individual Movies/ show covers*/
.release-list li {
  height: 400px;
  transition: transform 0.2s;
}
.release-list li:hover {
  transform: scale(1.05);
}
/* Mobile*/
@media screen and (max-width: 767px) {
  .top-releases:nth-child(2) {
    margin-bottom: 0;
  }
  .home h2 {
    margin: 0 auto 2em auto;
  }
}
