.sidebar {
  background-color: rgba(0, 0, 0, 0.1);
  text-align: left;
  white-space: nowrap;
}
.sidebar li {
  margin-bottom: 1.5em;
}
.sidebar a {
  transition: all 0.2s;
}
.selected {
  font-style: italic;
  margin-left: 1em;
  color: #FF6363;
}
/*Mobile*/
@media screen and(max-width: 767px) {
  .selected {
    margin: 0;
  }
  .sidebar li {
    margin: 0;
  }
}
