.store {
  display: flex;
  padding: 2.5em 2em;
}
.store aside {
  flex-basis: 15%;
}
.items {
  flex-basis: 85%;
}
.items ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3em;
}
.items li {
  transition: transform 0.2s;
}
.items li:hover {
  transform: scale(1.05);
}
.store .cover {
  height: 400px;
}
.store .price {
  margin-top: 0.5em;
  font-weight: 700;
  font-size: 1.3em;
}
/* Mobile*/
@media screen and (max-width: 767px) {
  .store {
    flex-direction: column;
    padding: 1.5em 0;
  }
  .store aside {
    margin-bottom: 2em;
  }
  .store aside ul {
    display: flex;
    justify-content: center;
    gap: 1.5em;
  }
  .items ul {
    flex-wrap: nowrap;
    justify-content: flex-start;
    overflow-x: scroll;
    padding: 0 1.5em;
  }
}
